import React from 'react'
import Feed from './Feed'
import PanelLeft from './PanelLeft'
import Link from 'next/link'
class Home extends React.Component{
        constructor(props){
            super(props)

            this.state={
                auth: ''
            }
        }
    componentDidMount(){
        const authToken = localStorage.getItem('auth-token')

        this.setState({
            auth: authToken
        })
    }
    render(){
        const style={
            display: 'grid',
            gridTemplateColumns: '25% 50% 25%',
            gridGap: '10px',
            marginLeft: '20px',
            marginRight: '20px'
        }
        return(
            <>
            {this.state.auth ? <div style={style}>
                <PanelLeft />
                <Feed />
                <p>2</p>
            </div> : <div>Here
                <Link to ='/login'>Login</Link>
            </div>}
            </>
        )
    }
}

export default Home